.admin-panel {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

h2 {
  text-align: center;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-top: 20px;
}

/* Responsivo: 1 columna en pantallas pequeñas */
@media (max-width: 768px) {
  .grid-container {
    grid-template-columns: 1fr;
  }
}

.admin-box {
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 10px;
}

.admin-box h3 {
  margin-top: 0;
}

.admin-box form {
  display: flex;
  flex-direction: column;
}

.admin-box input {
  margin-bottom: 10px;
  padding: 8px;
  font-size: 14px;
}

.admin-box button {
  padding: 10px;
  background-color: #008cba;
  color: white;
  border: none;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.2s ease;
}

.admin-box button:hover {
  background-color: #006494;
}

.delete-button {
  background-color: #e74c3c;
}

.delete-button:hover {
  background-color: #c0392b;
}

.edit-button {
  background-color: #ffd700;
  color: #000;
}

.edit-button:hover {
  background-color: #e6c200;
}

.admin-message {
  margin-top: 20px;
  text-align: center;
  font-weight: bold;
}

.success-message {
  color: green;
}

.error-message {
  color: red;
}